import helangCheckbox from "@/components/helang-checkbox/helang-checkbox.vue"
export default {
	components: {
	    "helang-checkbox":helangCheckbox
	},
	data() {
		return {
			title:'续费',
			loading:false,
			pickerIndex:-1,
			list: [{
				id:1,
				text:'一周',
				sontext:'30元',
				value:1
			},
			{
				id:2,
				text:'一个月',
				sontext:'150元',
				value:2
			},
			{
				id:3,
				text:'半年',
				sontext:'300元',
				value:3
			},
			{
				id:4,
				text:'一年',
				sontext:'500元',
				value:4
			}
			]
		}
	},
	onLoad(){
		
	},
	onShow(){
		this.$refs.checkbox.set({
			type:'radio',		// 类型：复选框
			column:2,				// 分列：3
			list:this.list	// 列表数据
		});
		this.pickerIndex = 3;
	},
	methods: {
		/* 创建列表数据 */
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},			
		/* 发起数据 */
		handleSubmit() {
			let data = this.$refs.checkbox.get();	// 组件返回的数据
		    console.log("发起支付:"+data.value);
		    this.loading = true;
		    uni.login({
		        success: (e) => {
		            console.log("login success", e);
		            uni.request({
		                url: `https://unidemo.dcloud.net.cn/payment/wx/mp?code=${e.code}&amount=${data.value}`,
		                success: (res) => {
		                    console.log("pay request success", res);
		                    if (res.statusCode !== 200) {
		                        uni.showModal({
		                            content: "支付失败，请重试！",
		                            showCancel: false
		                        });
		                        return;
		                    }
		                    if (res.data.ret === 0) {
		                        console.log("得到接口prepay_id", res.data.payment);
		                        let paymentData = res.data.payment;
		                        uni.requestPayment({
		                            timeStamp: paymentData.timeStamp,
		                            nonceStr: paymentData.nonceStr,
		                            package: paymentData.package,
		                            signType: 'MD5',
		                            paySign: paymentData.paySign,
		                            success: (res) => {
		                                uni.showToast({
		                                    title: "感谢您的赞助!"
		                                })
		                            },
		                            fail: (res) => {
		                                uni.showModal({
		                                    content: "支付失败,原因为: " + res
		                                        .errMsg,
		                                    showCancel: false
		                                })
		                            },
		                            complete: () => {
		                                this.loading = false;
		                            }
		                        })
		                    } else {
		                        uni.showModal({
		                            content: res.data.desc,
		                            showCancel: false
		                        })
		                    }
		                },
		                fail: (e) => {
		                    console.log("fail", e);
		                    this.loading = false;
		                    uni.showModal({
		                        content: "支付失败,原因为: " + e.errMsg,
		                        showCancel: false
		                    })
		                }
		            })
		        },
		        fail: (e) => {
		            console.log("fail", e);
		            this.loading = false;
		            uni.showModal({
		                content: "支付失败,原因为: " + e.errMsg,
		                showCancel: false
		            })
		        }
		    })
		}
	}
}
